.cont{
  font-size: 35px;
}
.contact {
  display: flex;
  text-decoration: none;
  list-style: none;
  flex-grow: 8;
  align-items: center;
}
.heading {
  color: rgb(148, 199, 46);
  padding: 5px;
  background-color: rgb(0, 0, 0, 0.7);
  border: 2px solid hotpink;
  border-radius: 10px;
  margin: 0px 15px;
  font-size: 35px;
}
.heading:hover {
  font-size: 40px;
  background-color: rgb(0, 0, 0, 1);
}
.info {
  color: whitesmoke;
  background-color: rgb(0, 0, 0, 0.5);
  padding: 10px;
  margin: 0px 15px;
  border-left: 2px double cyan;
  border-right: 2px double cyan;
  border-bottom: 2px double cyan;
}

.list {
  border-right: 3px solid yellow;
  border-left: 3px solid yellow;
  margin: 10px;
}
